

import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'

import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'


AjaxPlugin.$http.defaults.baseURL = '/api'
Vue.use(AjaxPlugin)
Vue.use(AlertPlugin)
Vue.use(ConfirmPlugin)
Vue.use(ToastPlugin)

require('es6-promise').polyfill()

Vue.config.productionTip = false

const whiteList = ['/login', '/process', '/process1']
router.beforeEach(function (to, from, next) {
console.log('跑到beforeEach了')
store.commit('SWITCH_IS_LOADING', true)

if (whiteList.indexOf(to.path) !== -1) {

next()
} else {

const nowTimestamp = new Date().getTime()

const cookieTimestamp = Number(store.getters.expires)


if (nowTimestamp < cookieTimestamp) {

next()
} else {
store.commit('SWITCH_IS_LOADING', false)

next('/login') 
}
}
})

router.afterEach(function (to) {
console.log('跑到afterEach了')
store.commit('SWITCH_IS_LOADING', false)
})


AjaxPlugin.$http.interceptors.request.use(function (config) {

if (store.getters.token) {

config.headers['x-lt-token'] = store.getters.token
}

store.commit('SWITCH_IS_LOADING', true)
return config
})


AjaxPlugin.$http.interceptors.response.use(function (res) {

store.commit('SWITCH_IS_LOADING', false)
return res.data.data
}, function (error) {
store.commit('SWITCH_IS_LOADING', false)
console.error(error.response.data.msg)
let msg = error.response.data.msg
Vue.$vux.alert.show({
title: '提示',
content: msg
})
return Promise.reject(error)
})

/* eslint-disable no-new */
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app')



