
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
components: {
Group,
XInput,
XButton,
Search,
Flexbox,
FlexboxItem,
Divider
},
beforeCreate: function () {
document.body.style.backgroundColor = '#004790'
},
beforeDestroy: function () {
document.body.style.backgroundColor = '#F3F3F5'
},
data () {
return {
searchInfo: {
customerCode: ''
},
showSearchResult: 0,

customerInfo: {
customerCode: '',
customerName: '',
status: '',
logID: '',
openDate: ''
}
}
},

created: function () {

this.$store.commit('SWITCH_IS_LOADING', false)
},
methods: {

initProcess: function () {
},

searchCustomer: function () {
const vm = this
if (!vm.$refs.refCustomerCode.valid) {
this.$vux.alert.show({
title: '提示',
content: '请输入证件号码'
})
} else {
vm.$http.get('/customer/getCustomer?customerCode=' + vm.searchInfo.customerCode)
.then(function (res) {
if (typeof (res) == 'undefined') {
vm.showSearchResult = 1
} else {
vm.showSearchResult = 2
vm.customerInfo = res
}
})
}
}
}
}



