
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
components: {
Group,
XInput,
XButton,
Search,
Flexbox,
FlexboxItem,
Divider,
Cell
},
data () {
return {
searchInfo: {
customerCode: ''
},
customerList: []
}
},

created: function () {
const vm = this
vm.initCustomer().then(function (res) {
vm.customerList = res
console.log(vm.customerList)
})
},
methods: {

initCustomer: function () {
const vm = this
return vm.$http.get('/customer/listCustomer')
},
searchCustomer: function () {
const vm = this
vm.$http.get('/customer/listCustomer?customerCode=' + vm.searchInfo.customerCode)
.then(function (res) {
vm.customerList = res
})
},
addCustomer: function () {
const vm = this
vm.$router.push('/customer/add')
},
modifyCustomer: function (id) {
const vm = this
vm.$router.push({path: '/customer/modify', query: { id: id }})
}
}
}



