
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Flow from 'vux/src/components/flow/flow.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
components: {
Group,
XInput,
XButton,
Search,
Flow,
FlowState,
FlowLine,
Flexbox,
FlexboxItem,
Divider
},
data () {
return {
searchInfo: {
customerCode: ''
},
showSearchResult: false,

customerInfo: {
customerCode: '',
customerName: '',
status: '',
logID: '',
openDate: ''
},
state: {
firstFlow: '',
secondFlow: '',
thirdFlow: '',
forthFlow: ''
}
}
},

created: function () {

this.$store.commit('SWITCH_IS_LOADING', false)
},
methods: {

initLogin: function () {
},

searchCustomer: function () {
const vm = this
vm.$http.get('/customer/getCustomer?customerCode=' + vm.searchInfo.customerCode)
.then(function (res) {
console.log(res)
vm.showSearchResult = true
vm.customerInfo = res
console.log(vm.customerInfo)
})
}
}
}



