
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'

export default {
components: {
XInput,
Group,
PopupPicker,
XButton,
Flexbox,
FlexboxItem
},
data () {
return {
addInfo: {
customerCode: '',
customerName: '',
status: ['1'],
openDate: ''
},
statuslist: [[{
name: '预开户',
value: '1'
}, {
name: '待核准',
value: '2'
}, {
name: '待激活',
value: '3'
}, {
name: '已完成',
value: '4'
}]]
}
},

created: function () {
},
methods: {

initCustomer: function () {
},
submitCustomer: function () {
const vm = this
vm.$http.post('/customer/addCustomer', querystring.stringify(vm.addInfo))
.then(function (res) {
vm.onSuccess(res)
})
},
onSuccess: function (res) {
const vm = this
vm.$vux.toast.show({
time: 1000,
text: '添加成功',
onHide: function () {
vm.$router.push('/customer/list')
}
})
}
}
}



