
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'
import querystring from 'vux/src/tools/querystring/index.js'

export default {
components: {
Group,
XInput,
XButton,
Box
},
data () {
return {




loginInfo: {
userName: '',
loginPassword: ''
},
isDisable: false,
isLoading: false,
btnContent: '登录'
}
},

created: function () {
},
methods: {

initLogin: function () {
},
beforeSubmit: function () {
const vm = this
vm.isDisable = true
vm.isLoading = true
vm.btnContent = '正在登录...'
},
submitLogin: function () {
const vm = this
vm.beforeSubmit()
vm.$http.post('/admin/login', querystring.stringify(vm.loginInfo))
.then(function (res) {
vm.onSuccess(res)
}).catch(function (err) {
vm.onError(err)
})
},
onSuccess: function (res) {
const vm = this

vm.isDisable = vm.isLoading = false
vm.btnContent = '登录'

cookie.set('userId', res.userId)
cookie.set('token', res.token)
cookie.set('tokenExpired', res.tokenExpired)

vm.$store.commit('SET_USER_ID', res.userId)
vm.$store.commit('SET_TOKEN', res.token)
vm.$store.commit('SET_EXPIRES', res.tokenExpired)
vm.$vux.toast.show({
time: 1000,
text: '登陆成功',
onHide: function () {
vm.$router.push('/customer/list')
}
})
},
onError: function () {
const vm = this
vm.isDisable = vm.isLoading = false
vm.btnContent = '登录'
}
}
}



