
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'

export default {
components: {
XInput,
Group,
PopupPicker,
XButton,
Flexbox,
FlexboxItem
},
data () {
return {
customerInfo: {
id: '',
customerCode: '',
customerName: '',
status: [],
openDate: ''
},
statuslist: [[{
name: '预开户',
value: '1'
}, {
name: '待核准',
value: '2'
}, {
name: '待激活',
value: '3'
}, {
name: '已完成',
value: '4'
}]]
}
},

created: function () {
const vm = this
vm.initCustomer().then(function (data) {
vm.customerInfo.id = data.id
vm.customerInfo.customerCode = data.customerCode
vm.customerInfo.customerName = data.customerName
vm.customerInfo.status = [data.status]
vm.customerInfo.openDate = data.openDate
})
},
methods: {

initCustomer: function () {
const vm = this
vm.customerInfo.id = vm.$route.query.id
return vm.$http.get('/customer/initCustomer?customerId=' + vm.customerInfo.id)
},
modifyCustomer: function () {
const vm = this
vm.$http.post('/customer/modifyCustomer', querystring.stringify(vm.customerInfo))
.then(function (res) {
vm.$vux.toast.show({
time: 1000,
text: '修改成功',
onHide: function () {
vm.$router.push('/customer/list')
}
})
})
},
deleteCustomer: function () {
const vm = this
const params = {
id: vm.customerInfo.id
}
vm.$http.post('/customer/deleteCustomer', params)
.then(function (res) {
vm.$vux.toast.show({
time: 1000,
text: '删除成功',
onHide: function () {
vm.$router.push('/customer/list')
}
})
})
}
}
}



